import React from 'react'
import { useSelector } from 'react-redux'

import { formatSigned, formatTimestamp } from '../../utils/format'

import styles from './Cell.module.scss'

const CellItem = ({ funding_24h, funding_time, fetchTime }) => {
  const now = useSelector(state => state.clock.now)
  const displayTs = funding_time * 1000 - (now - fetchTime)
  const { timeString, isoString } = formatTimestamp(displayTs / 1000)

  return (
    <li className={styles['cell-item']}>
      <span>{formatSigned(funding_24h)}</span>
      <time dateTime={isoString}>{timeString}</time>
    </li>
  )
}

export default CellItem