import { createSelector } from '@reduxjs/toolkit'

import { selectFilters } from '../filters/selectors'

export const selectItems = (state) => state.items.items
export const selectStatus = (state) => state.items.status

export const selectVisibleItems = createSelector(
	[selectItems, selectFilters],
	(items, filters) => {
		return items
			// Осталось: strategy
			// 1) По строке поиска
			.filter(item => item.coin.toLowerCase().includes(filters.search.toLowerCase()))
			// 2) По выбранным биржам
			.filter(item => {
				if (filters.exchanges.length === 0) return true
				return item.pairs.some(p => filters.exchanges.map(e => e.toUpperCase()).includes(p.exchange))
			})
			// 3) Фильтрация по diffIntervals: сравнение funding_period
			.filter(item => {
				const { diffIntervals } = filters
				const [p0, p1] = item.pairs
				// если diffIntervals=false, оставляем только равные периоды
				if (!diffIntervals) return p0.funding_period === p1.funding_period
				// если diffIntervals=true, оставляем только разные периоды
				return p0.funding_period !== p1.funding_period
			})
			// 4) Фильтрация по mValue: разница во времени funding_time в минутах
			.filter(item => {
				const { mValue } = filters
				if (!mValue) return true
				const [p0, p1] = item.pairs
				const diffMinutes = Math.abs(p0.funding_time - p1.funding_time) / 60
				return diffMinutes >= Number(mValue)
			})
			// 4) Сортировка
			.sort((a, b) => {
				const dir = filters.sortDir === 'asc' ? 1 : -1
				switch (filters.sortBy) {
					case 'coin':
						return dir * a.coin.localeCompare(b.coin)
					case 'priceDiff':
						const aPrice0 = a.pairs[0].position === 'LONG' ? a.pairs[0].price : -a.pairs[0].price
						const aPrice1 = a.pairs[1].position === 'LONG' ? a.pairs[1].price : -a.pairs[1].price
						const bPrice0 = b.pairs[0].position === 'LONG' ? b.pairs[0].price : -b.pairs[0].price
						const bPrice1 = b.pairs[1].position === 'LONG' ? b.pairs[1].price : -b.pairs[1].price

						return dir * ((aPrice0 - aPrice1) - (bPrice0 - bPrice1))
					case 'fundingDiff':
						const fundingA = a.pairs[0].funding - a.pairs[1].funding
						const fundingB = b.pairs[0].funding - b.pairs[1].funding

						return dir * (fundingA - fundingB)
					case 'funding24h':
						const funding24hA = a.pairs[0].funding_24h + a.pairs[1].funding_24h
						const funding24hB = b.pairs[0].funding_24h + b.pairs[1].funding_24h

						return dir * (funding24hA - funding24hB)
					case 'f_spread':
						return dir * (a.f_spread - b.f_spread)
					case 'open_spread':
						return dir * (a.open_spread - b.open_spread)
					default:
						return 0
				}
			})
	}
)